import React, { useState } from 'react';
import { Search, FileText } from 'lucide-react';

const VerifiedUserPanel = () => {
  const [searchTerm, setSearchTerm] = useState('');
  
  const initialCards = [
    {
      id: 1,
      description: "Moldura Decorativa",
      moldingNumber: "MD-001",
      category: "Clásica",
      image: "/api/placeholder/200/200"
    },
    {
      id: 2,
      description: "Cornisa Moderna",
      moldingNumber: "CM-002",
      category: "Moderna",
      image: "/api/placeholder/200/200"
    },
    {
      id: 3,
      description: "Marco Victorian",
      moldingNumber: "MV-003",
      category: "Vintage",
      image: "/api/placeholder/200/200"
    },
    {
      id: 4,
      description: "Moldura Colonial",
      moldingNumber: "MC-004",
      category: "Colonial",
      image: "/api/placeholder/200/200"
    },
    {
      id: 5,
      description: "Cornisa Minimalista",
      moldingNumber: "CM-005",
      category: "Moderna",
      image: "/api/placeholder/200/200"
    },
    {
      id: 6,
      description: "Marco Barroco",
      moldingNumber: "MB-006",
      category: "Clásica",
      image: "/api/placeholder/200/200"
    }
  ];

  const [cards, setCards] = useState(initialCards);

  const handleSearch = (e) => {
    const value = e.target.value;
    setSearchTerm(value);
    
    if (value === '') {
      setCards(initialCards);
    } else {
      const filtered = initialCards.filter(card => 
        card.description.toLowerCase().includes(value.toLowerCase()) ||
        card.moldingNumber.toLowerCase().includes(value.toLowerCase()) ||
        card.category.toLowerCase().includes(value.toLowerCase())
      );
      setCards(filtered);
    }
  };

  const handlePDFView = () => {
    window.location.href = '/pdfver';
  };

  const handleContact = () => {
    window.location.href = '/contact';
  };

  return (
    <div className="flex gap-6 p-24 min-h-screen bg-[#697565]/10">
      {/* Left Section */}
      <div className="w-3/4 space-y-6">
        <div className="p-4 bg-white/80 backdrop-blur-sm border border-[#697565]/20 rounded-lg shadow-sm">
          <div className="relative">
            <input
              type="text"
              placeholder="Buscar por descripción, número o categoría..."
              value={searchTerm}
              onChange={handleSearch}
              className="w-full pl-10 pr-4 py-2 border border-[#697565]/30 rounded-md 
                         focus:outline-none focus:ring-2 focus:ring-[#697565]/50 focus:border-[#697565]
                         placeholder-gray-400 text-gray-800"
            />
            <Search className="absolute left-3 top-2.5 h-5 w-5 text-[#697565]" />
          </div>
        </div>

        <div className="grid grid-cols-2 gap-4 lg:grid-cols-3">
          {cards.map((card) => (
            <div key={card.id} 
                 className="overflow-hidden hover:shadow-lg transition-all duration-300 
                          bg-white/80 backdrop-blur-sm border border-[#697565]/20 
                          hover:border-[#697565]/40 rounded-lg">
              <img
                src={card.image}
                alt={card.description}
                className="w-full h-48 object-cover"
              />
              <div className="p-4">
                <h3 className="font-semibold mb-2 text-[#2D2F2B]">{card.description}</h3>
                <p className="text-sm text-[#697565]">Número: {card.moldingNumber}</p>
                <p className="text-sm text-[#697565]">Categoría: {card.category}</p>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Right Section */}
      <div className="w-1/4">
        <div className="p-6 space-y-6 bg-gradient-to-br from-[#697565]/90 to-[#2D2F2B]/90 
                      backdrop-blur-sm border border-[#697565]/20 rounded-lg">
          <button 
            onClick={handlePDFView}
            className="w-full h-16 flex items-center justify-center gap-2 
                     bg-white/10 hover:bg-white/20 transition-all duration-300 
                     border border-white/20 hover:border-white/30 text-white rounded-lg
                     focus:outline-none focus:ring-2 focus:ring-white/50"
          >
            <FileText className="h-6 w-6" />
            <span>Ver Catálogo PDF</span>
          </button>

          <button 
            onClick={handleContact}
            className="w-full h-16 flex items-center justify-center gap-2 
                     bg-white/10 hover:bg-white/20 transition-all duration-300 
                     border border-white/20 hover:border-white/30 text-white rounded-lg
                     focus:outline-none focus:ring-2 focus:ring-white/50"
          >
            <span>Contacto</span>
          </button>
        </div>
      </div>
    </div>
  );
};

export default VerifiedUserPanel;