import React from 'react';
import nuevaa1 from '../img/nuevaa1.png';
import nuevaa2 from '../img/nuevaa2.png';
import nuevoo3 from '../img/nuevoo3.png';
import nuevoo4 from '../img/nuevoo4.png';
import nuevaa4 from '../img/nuevaa4.png';
import './CardsSection.css';

const CardsSection = () => {
  return (
    <div className="cards-section">
      {/* Contenedor para las cartas pequeñas */}
      <div className="cards-grid">
        <div className="card small-card">
          <img src={nuevaa1} alt="Card 1" className="card-image" />
        </div>
        <div className="card small-card">
          <img src={nuevaa2} alt="Card 2" className="card-image" />
        </div>
        <div className="card small-card">
          <img src={nuevoo3} alt="Card 3" className="card-image" />
        </div>
        <div className="card small-card">
          <img src={nuevoo4} alt="Card 4" className="card-image" />
        </div>
      </div>

      {/* Carta grande */}
      <div className="card large-card">
        <img src={nuevaa4} alt="Large Card" className="card-image" />
      </div>
    </div>
  );
};

export default CardsSection;
