import React from 'react';
import { Phone, Mail, MapPin } from 'lucide-react';
import '../ContactForm.css'; // Asegúrate de tener este archivo CSS

const ContactForm = () => {
  return (
    <div className="contact-container">
      <h1> </h1>
      
      <div className="contact-card">
        <div className="contact-grid">
          {/* Lado izquierdo - Información de contacto */}
          <div className="contact-info">
            <h3>Información de Contacto</h3>
            <p className="contact-desc">
              Completa el formulario y nos pondremos en contacto contigo dentro de las 24 horas.
            </p>
            
            <div className="contact-details">
              <div className="contact-item">
                <Phone className="icon" />
                <span>+52 618 824 6172</span>
              </div>
              <div className="contact-item">
                <Mail className="icon" />
                <span>megnasa21@gmail.com</span>
              </div>
              <div className="contact-item">
                <MapPin className="icon" />
                <span>Durango, México</span>
              </div>
            </div>

            {/* Mapa de Google Maps */}
            <div className="map-placeholder">
              <iframe 
                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d58302.70144715295!2d-104.6562962074707!3d24.0339269832418!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x869bb7a518bcdf73%3A0xbfdd8ea63a6c4ad4!2sDurango%2C%20Dgo.!5e0!3m2!1ses-419!2smx!4v1733085783592!5m2!1ses-419!2smx"
                width="100%"
                height="100%"
                style={{ border: 0, borderRadius: '0.5rem' }}
                allowFullScreen=""
                loading="lazy"
                referrerPolicy="no-referrer-when-downgrade"
              ></iframe>
            </div>

            <div className="decorative-circles">
              <div className="circle circle-1"></div>
              <div className="circle circle-2"></div>
            </div>
          </div>

          {/* Lado derecho - Formulario */}
          <div className="form-container">
            <form onSubmit={(e) => e.preventDefault()}>
              <div className="form-fields">
                <input
                  type="text"
                  placeholder="Tu Nombre"
                />
                <input
                  type="email"
                  placeholder="Correo Electrónico"
                />
                <input
                  type="tel"
                  placeholder="Número de Teléfono"
                />
                <textarea
                  rows="4"
                  placeholder="Tu Mensaje"
                ></textarea>
              </div>

              <button type="submit">
                Enviar Mensaje
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ContactForm;
