import React, { useState } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';

const LanguageSwitcher = () => {
  const [language, setLanguage] = useState('en'); // Idioma predeterminado: inglés
  const navigate = useNavigate(); // Hook de React Router para redirigir
  const location = useLocation(); // Hook para obtener la ruta actual

  const handleToggleLanguage = () => {
    const currentPath = location.pathname; // Obtiene la ruta actual

    if (language === 'en') {
      setLanguage('es');
      // Verifica si la ruta ya tiene el prefijo "/es/"
      if (!currentPath.startsWith('/es')) {
        navigate(`/es${currentPath === '/' ? '/home' : currentPath}`);
      }
    } else {
      setLanguage('en');
      // Elimina el prefijo "/es/" si está presente
      navigate(currentPath.replace('/es', '') || '/');
    }
  };

  return (
    <div style={styles.switchContainer}>
      <label style={styles.label}>
        <input
          type="checkbox"
          onChange={handleToggleLanguage}
          checked={language === 'es'}
          style={styles.checkbox}
        />
        <span style={styles.switch}>
          {language === 'en' ? 'EN' : 'ES'}
        </span>
      </label>
    </div>
  );
};


// Estilos en línea ajustados
const styles = {
  switchContainer: {
    display: 'flex',
    alignItems: 'center',
    marginLeft: '1rem',
  },
  label: {
    cursor: 'pointer',
    display: 'flex',
    alignItems: 'center',
  },
  checkbox: {
    display: 'none',
  },
  switch: {
    display: 'inline-block',
    padding: '5px 10px',
    backgroundColor: '#697565',
    color: '#fff',
    borderRadius: '25px',
    fontWeight: 'bold',
    userSelect: 'none',
    fontSize: '0.9rem',

    transition: 'background-color 0.3s',
  },
};

export default LanguageSwitcher;
