import React from 'react';
import { useNavigate } from 'react-router-dom';

const ProductsCrowns = () => {
  const navigate = useNavigate();

  const crownsProducts = [
    {
      moldingNumber: "M001",
      description: "Classic molding with a traditional design, perfect for elegant spaces.",
      imagen: "/api/placeholder/600/600"
    },
    {
      moldingNumber: "M002",
      description: "Modern molding with clean lines and a minimalist finish.",
      imagen: "/api/placeholder/600/600"
    },
    {
      moldingNumber: "M003",
      description: "Victorian molding with ornate details and refined finishes.",
      imagen: "/api/placeholder/600/600"
    },
    {
      moldingNumber: "M004",
      description: "Rustic molding with a natural texture and handcrafted look.",
      imagen: "/api/placeholder/600/600"
    },
    {
      moldingNumber: "M005",
      description: "Contemporary molding with geometric design and matte finish.",
      imagen: "/api/placeholder/600/600"
    },
    {
      moldingNumber: "M006",
      description: "Decorative molding with floral patterns and intricate details.",
      imagen: "/api/placeholder/600/600"
    },
    {
      moldingNumber: "M007",
      description: "Minimalist molding with a subtle profile and versatile design.",
      imagen: "/api/placeholder/600/600"
    }
  ];

  const handleContactClick = () => {
    navigate('/contact');
  };

  return (
    <div className="container mx-auto px-4 py-20">
      <h1 className="text-3xl font-bold text-center mb-8">Crown Molding Collection</h1>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {crownsProducts.map((product) => (
          <div
            className="bg-white rounded-lg shadow-md overflow-hidden transform transition-transform duration-300 hover:scale-105 cursor-pointer flex flex-col h-full"
            key={product.moldingNumber}
          >
            <img
              src={product.imagen}
              alt={`Crown molding ${product.moldingNumber}`}
              className="w-full h-48 object-cover"
            />
            <div className="p-4 flex flex-col flex-grow">
              <h5 className="text-xl font-semibold text-gray-800 mb-2">
                Crown Molding {product.moldingNumber}
              </h5>
              <p className="text-sm text-gray-600 leading-relaxed flex-grow">
                {product.description}
              </p>
            </div>
          </div>
        ))}

        {/* Informative Card */}
        <div
          className="md:col-span-2 bg-emerald-800 rounded-lg shadow-md p-6 flex flex-col justify-between cursor-pointer hover:bg-emerald-700 transition-colors duration-300"
          onClick={handleContactClick}
        >
          <div>
            <h5 className="text-xl font-bold text-emerald-50 mb-4">Need More Information?</h5>
            <p className="text-emerald-100 mb-4">
              Interested in our crown moldings? Want to discuss custom designs or get a quote? 
              Our team is ready to help you find the perfect molding for your project.
            </p>
            <p className="text-emerald-100">
              To view the complete list of products, please register to obtain your access token (key).
            </p>
          </div>
          <div className="mt-4">
            <span className="text-emerald-50 font-semibold hover:underline">
              Contact us →
            </span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductsCrowns;
