import React from 'react';
import LoginPage, { Username, Password, Submit, Title, Logo } from '@react-login-page/page5';
import '../CustomLogin.css'; // Asegúrate de que esta ruta sea correcta

const styles = { height: 560 };

const Demo = () => (
  <div style={styles}>
    <LoginPage>
      {/* Campos de entrada */}
      <Username name="userUserName" placeholder="Ingresa un Token" />
      

      {/* Solo el botón de Submit */}
      <Submit>Entrar</Submit>

      {/* Título del formulario */}
      <Title visible={false}/>

      {/* El logo es oculto */}
      <Logo visible={false} />
    </LoginPage>
  </div>
);

export default Demo;

