import React, { useEffect, useRef } from 'react';

const SecurePDFViewer = () => {
  const pdfUrl = 'https://firebasestorage.googleapis.com/v0/b/c-and-f.appspot.com/o/2020_SW_Profile_guide_8-13-21_speads.pdf?alt=media&token=6f3d1338-97d3-4a22-9bae-09a5512b54ef';
  const containerRef = useRef(null);

  const preventRightClick = (e) => {
    e.preventDefault();
    e.stopPropagation();
    return false;
  };

  const preventTouchActions = (e) => {
    // Prevenir pellizcado, deslizamiento y otros gestos táctiles
    e.preventDefault();
    e.stopPropagation();
    return false;
  };

  useEffect(() => {
    const container = containerRef.current;

    // Función para manejar atajos de teclado
    const handleKeyDown = (e) => {
      const restrictedShortcuts = [
        (e.ctrlKey && e.key === 's'),   // Ctrl + S
        (e.ctrlKey && e.key === 'p'),   // Ctrl + P
        (e.ctrlKey && e.key === 'c'),   // Ctrl + C
        (e.ctrlKey && e.key === 'v'),   // Ctrl + V
        (e.key === 'PrintScreen'),      // Tecla Impr Pant
        (e.ctrlKey && e.key === 'a')    // Ctrl + A (seleccionar todo)
      ];

      if (restrictedShortcuts.some(Boolean)) {
        e.preventDefault();
        e.stopPropagation();
        return false;
      }
    };

    // Añadir múltiples capas de prevención de clic derecho y gestos táctiles
    if (container) {
      // Prevenir clic derecho en el contenedor
      container.oncontextmenu = preventRightClick;

      // Prevenir clic derecho en el documento completo
      document.body.oncontextmenu = preventRightClick;

      // Prevenir atajos de teclado en el contenedor
      container.addEventListener('keydown', handleKeyDown);

      // Prevenir acciones táctiles en el contenedor
      container.addEventListener('touchstart', preventTouchActions);
      container.addEventListener('touchmove', preventTouchActions);
      container.addEventListener('touchend', preventTouchActions);
    }

    // Deshabilitar selección de texto
    const disableSelection = (e) => {
      e.preventDefault();
    };

    if (container) {
      container.addEventListener('selectstart', disableSelection);
      container.style.userSelect = 'none';
      container.style.webkitUserSelect = 'none';
    }

    // Limpiar eventos al desmontar el componente
    return () => {
      if (container) {
        container.oncontextmenu = null;
        document.body.oncontextmenu = null;
        container.removeEventListener('keydown', handleKeyDown);
        container.removeEventListener('touchstart', preventTouchActions);
        container.removeEventListener('touchmove', preventTouchActions);
        container.removeEventListener('touchend', preventTouchActions);
        container.removeEventListener('selectstart', disableSelection);
      }
    };
  }, []);

  return (
    <div 
      ref={containerRef}
      className="relative w-full h-screen"
      onContextMenu={preventRightClick}
      onTouchStart={preventTouchActions}
      onTouchMove={preventTouchActions}
      onTouchEnd={preventTouchActions}
    >
      <div className="absolute inset-0 flex items-center justify-center">
        <div className="w-full h-full relative">
          <div 
            className="absolute inset-0 overflow-auto"
            style={{
              pointerEvents: 'auto',
              userSelect: 'none',
              webkitUserSelect: 'none'
            }}
          >
            <iframe
              src={`${pdfUrl}#toolbar=0&navpanes=0`}
              className="w-full h-full"
              style={{
                border: 'none',
                pointerEvents: 'auto',
                userSelect: 'none',
                webkitUserSelect: 'none'
              }}
              title="PDF Scrollable"
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default SecurePDFViewer;