import React, { useState } from 'react';
import LoginPage, { 
  Username, 
  Submit, 
  Title, 
  Logo 
} from '@react-login-page/page5';

const LoginWithoutCaptcha = () => {
  const [error, setError] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');

    try {
      const token = e.target.userToken.value;

      if (!token) {
        setError('Please enter your token');
        return;
      }

      const response = await loginUser({ token });
      console.log('Login successful:', response);

    } catch (error) {
      console.error('Error during login:', error);
      setError('Error logging in. Please try again.');
    }
  };

  const loginUser = async (credentials) => {
    return new Promise((resolve) => {
      setTimeout(() => {
        resolve({ success: true });
      }, 1000);
    });
  };

  return (
    <div className="w-full max-w-md mx-auto px-40 py-17 mt-28 text-center">
      <LoginPage onSubmit={handleSubmit}>
        {/* Título de login, centrado y grande */}

        {error && (
          <div className="mb-4 text-red-500 text-sm">
            {error}
          </div>
        )}

        <div className="flex justify-center mb-6">
          <Username
            name="userToken"
            placeholder="Enter your token"
            className="text-center w-80 border border-gray-300 p-3 rounded-lg text-xl"
            required
          />
        </div>

        <Submit className="w-full bg-blue-600 hover:bg-blue-700 text-white font-bold py-3 px-6 rounded-lg text-lg">
          Login
        </Submit>

        <Title visible={false} />
        <Logo visible={false} />
      </LoginPage>
    </div>
  );
};

export default LoginWithoutCaptcha;

